
#header_main{
    display: flex;
    width: 100%;
    height: 100%;
    border-bottom: 1px solid rgba(233, 235, 235, 0.692);
    align-items: center;
}
#dashboard{
    padding: 2%;
    width: 30%;
    height: 100%;
    margin-left: 2%;

}
#dashboard h1{
    font-family: cursive;
    font-size: 100%;
    
}
#dashboard p{
    font-family: cursive;
    font-size: 60%;
    opacity: 0.5;
}
#search{
    display: flex;
    height: 33px;
    width: 300px;
    background-color: rgb(245, 248, 252);
    padding: 1% 2%;
    border-radius: 20px;
    /* background-color: aquamarine; */
    align-items: center;
}
.header_icon{
    opacity: 0.5;
    
}
#search input{
    width: 80%;
    padding: 2% ;
    margin: 0 10px;
    border-radius: 10px;
    background-color: rgb(245, 248, 252);
    outline: none;
    border: none;
}
#search input:focus{
    color: rgb(125, 143, 140);
}
#search input::placeholder{
    opacity: 0.5;
    font-size: smaller;
    
}
#header_ul{
    margin-left: 280px;
    width: 15%;
    min-width: 120px;
    
}

#header_ul li{
    position: relative;
    list-style: none;
    display: inline;
    margin: 3px;
}

.li_logo{
    display: inline-block;
    width: 28px;
    height: 28px; 
    border-radius: 50%;
    background-color: rgb(223, 229, 238);
    text-align: center;
    line-height: 28px;
    opacity: 0.5;
}
#img{
    display: inline-block;
    position: relative;
    top: 9px;
    left: 3px;
}
li img{
    width: 28px;
    height: 28px;
    border-radius: 50%;
}